<template>
  <div class="my_l">
    <header class="top_bar">
      <a onclick="window.history.go(-1)" class="icon_back"></a>
      <h3 class="cartname">京东登录</h3>
    </header>
    <main class="user_login_box">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
        <el-form-item prop="phone">
          <el-input
            type="text"
            maxlength="11"
            v-model="ruleForm.phone"
            placeholder="请输入手机号"
          ></el-input>
        </el-form-item>
        <el-form-item prop="checknum" class="yzm">
          <el-input
            type="text"
            v-model="ruleForm.checknum"
            placeholder="请输入验证码"
          ></el-input>
          <span v-if="!countDown" :class="{phoneRight:phoneRight}" @click="getVerifyCode()">获取验证码</span>
          <span v-else :style="{display:distime}" >重新发送({{this.countDown}}s)</span>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login('ruleForm', ruleForm.phone, ruleForm.checknum)">登录</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="go_reg_box">
        <router-link tag="span" to="/loginPwd">帐号密码登录</router-link>
        <router-link tag="span" to="/register">快速注册</router-link>
      </div>
    </main>
  </div>
</template>

<script>
// import { getPhoneCode } from '../api/index'
import { Dialog } from 'vant'
import { mapActions } from 'vuex'
export default {
  data () {
    var validatePhone = (rule, value, callback) => {
      if (!value) {
        callback(new Error('手机号不能为空'))
      } else {
        // if (/^1[3456789]\d{9}$/.test(value)) {
        callback()
        // } else {
        //   callback(new Error('手机号码错误'))
        // }
      }
    }
    var checknum = (rule, value, callback) => {
      if (!value) {
        callback(new Error('验证码不能为空'))
      } else if (!(/^\d{4}$/gi.test(value))) {
        callback(new Error('验证码错误'))
      } else {
        callback()
      }
    }
    return {
      distime: 'none',
      phoneRight: true,
      countDown: 0,
      userInfo: {},

      ruleForm: {
        phone: '',
        checknum: ''
      },
      rules: {
        phone: [{ validator: validatePhone, trigger: 'blur' }],
        checknum: [{ validator: checknum, trigger: 'blur' }]
      }
    }
  },
  mounted () {
    // console.log(process.env)
  },
  methods: {
    ...mapActions(['syncUserInfo']),
    async getVerifyCode () {
      const phone = this.ruleForm.phone
      this.distime = 'inline'
      this.countDown = 1
      // 获取短信验证码
      const result = await this.$http.get('/send_code', { phone })
      this.intervalId = setInterval(() => {
        this.countDown--
        if (this.countDown === 0) {
          clearInterval(this.intervalId)
          Dialog.confirm({
            title: '标题',
            message: '验证码:' + result.data.code
          })
        }
      }, 1000)
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    async login (formName, phone, checknum) {
      // 手机验证码登录
      await this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post('/login_code', { phone, checknum }).then((data) => {
            // console.log(data.data, 'res data')
            if (data.data.success_code === 200) {
              this.userInfo = data.data.message
              // console.log(this.userInfo, 'userInfo')
              // 6.1 同步用户的信息
              localStorage.setItem('token', this.userInfo)
              // console.log(this.userInfo.id, 2222222222222222)
              this.syncUserInfo(this.userInfo)
              // this.$router.push('/mine')
              // 6.2 回到主界面
              this.$router.back()
            } else {
              this.userInfo = {
                message: '登录失败，手机号或验证码不正确！'
              }
            }
          })
        } else {
          // console.log('验证失败')
          return false
        }
      })
    }
  }
}
</script>
<style>
  @import '../assets/css/login.css'
</style>
